/* eslint-disable react/no-find-dom-node */
import * as React from 'react';
import styled from 'styled-components';
import { ToolbarTableDeleteNormal, Tooltip } from '@ali/we-design';
import { ButtonWrapper, IconWrapper } from '../styled';

export const DeleteWrapper = styled(ButtonWrapper)`
  &:hover {
    color: rgba(255, 0, 0, 0.67);
    box-sizing: border-box;
  }
`;

interface ToolbarMenuProps {
  mode: 'row' | 'col';
  locale: any;
  scale: number;
  onDelete: () => void;
  onHighlightSelection: () => void;
  onCancelHighlightSelection: () => void;
}

export default class InlineToolbarDeleteButton extends React.Component<ToolbarMenuProps> {
  handleMouseDown = (e) => {
    e.preventDefault();
    e.stopPropagation();
    const { onDelete } = this.props;
    onDelete();
  };

  handleMouseEnter = () => {
    const { onHighlightSelection } = this.props;
    onHighlightSelection();
  };

  handleMouseLeave = () => {
    const { onCancelHighlightSelection } = this.props;
    onCancelHighlightSelection();
  };

  componentWillUnmount() {
    const { onCancelHighlightSelection } = this.props;
    onCancelHighlightSelection();
  }

  render() {
    const { mode, locale, scale } = this.props;
    const placement = mode === 'row' ? 'left' : 'top';
    const tip = mode === 'row' ? locale.toolbarDeleteRow : locale.toolbarDeleteCol;
    return (
      <DeleteWrapper
        scale={scale}
        onMouseDown={this.handleMouseDown}
        onMouseEnter={this.handleMouseEnter}
        onMouseLeave={this.handleMouseLeave}
        data-testid={`table-delete-${mode}-button`}
        size={16}
      >
        <Tooltip placement={placement} title={tip}>
          <IconWrapper>
            <ToolbarTableDeleteNormal style={{
              fontSize: 16 / scale,
              width: `${16 / scale}px`,
              height: `${16 / scale}px`,
            }}
            />
          </IconWrapper>
        </Tooltip>
      </DeleteWrapper>
    );
  }
}
